.social {
    display: flex;
    flex: 1;
    justify-content: flex-end;
    align-items: flex-end;

    &__list {
        display: flex;
        list-style-type: none;
    }

    &__item {
        padding-right: 2rem;
    }

    &__link {
        text-decoration: none;
        color: rgb(206, 214, 222);
    }
}